import React, { useEffect } from "react"
import Layout from "../../components/Layout"
import { AnimatedCursor } from "../../components/utils/AnimatedCursor"
import AOS from "aos"
import "aos/dist/aos.css"
import Driverbee from "../../components/Driverbee"
export default function TrackHero() {
  useEffect(() => {
    AOS.init()
  }, [])
  const mouseCoords =
    typeof window !== "undefined" &&
    JSON.parse(window.localStorage.getItem("mouseCoords"))

  return (
    <>
      <Layout>
        <Driverbee />
      </Layout>
      <AnimatedCursor
        coord={mouseCoords}
        innerSize={16}
        outerSize={16}
        color="255, 255, 255"
        outerAlpha={0.2}
        innerScale={0.8}
        outerScale={4}
        clickables={[
          "a",
          'input[type="text"]',
          'input[type="email"]',
          'input[type="number"]',
          'input[type="submit"]',
          'input[type="image"]',
          "label[for]",
          "select",
          "textarea",
          "button",
          ".link",
          ".swiper-pagination-bullet",
        ]}
      />
    </>
  )
}
